// 
export function getTodayFirstDay() {
  var y = new Date().getFullYear(); // 
  var m = new Date().getMonth() + 1; // 
  var d = new Date().getDate(); // 
  m = m < 10 ? '0' + m : m; //  0
  d = d < 10 ? '0' + d : d; //  0

  return [y, m, d].join('-') + ' 00:00:00';
}

//  
export function timestampGetTodayFirstDay(dataTime) {
  const time = new Date(dataTime);
  var y = time.getFullYear(); // 
  var m = time.getMonth() + 1; // 
  var d = time.getDate(); // 
  m = m < 10 ? '0' + m : m; //  0
  d = d < 10 ? '0' + d : d; //  0

  return [y, m, d].join('-') + ' 00:00:00';
}

