// 
export function getSearchToday(num) {
  var date = new Date(new Date().getTime() - (60 * 60 * 1000 * 24) * num);
  var y = date.getFullYear(); // 
  var m = date.getMonth() + 1; // 
  var d = date.getDate(); // 
  m = m < 10 ? '0' + m : m; //  0
  d = d < 10 ? '0' + d : d; //  0
  return [y, m, d].join('-');
}

//  
export function timestampGetSearchToday(num, dataTime) {
  var date = new Date(dataTime - (60 * 60 * 1000 * 24) * num);
  var y = date.getFullYear(); // 
  var m = date.getMonth() + 1; // 
  var d = date.getDate(); // 
  m = m < 10 ? '0' + m : m; //  0
  d = d < 10 ? '0' + d : d; //  0
  return [y, m, d].join('-');
}

// 
export function getTimeFunc(param) {
  var date = new Date(param);
  var y = date.getFullYear(); // 
  var m = date.getMonth() + 1; // 
  var d = date.getDate(); // 
  m = m < 10 ? '0' + m : m; //  0
  d = d < 10 ? '0' + d : d; //  0

  return [y, m, d].join('-');
}

// 
export function getStartTime() {
  var date = new Date();
  date.setDate(1);// 
  var year = date.getFullYear(); // 
  let month = date.getMonth() + 1;// 0-11+1
  month = month > 10 ? month : '0' + month;// 
  var day = date.getDate();// setDate(1) 
  day = day < 10 ? '0' + day : day; //  0

  // console.log(month) 03
  return year + '-' + month + '-' + day;// 
};

// 
export function getEndTime() {
  var date = new Date();
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  month = month > 10 ? month : '0' + month;// 
  // 0-11
  var nextMonthFirthDay = new Date(year, month, 1);// 
  console.log(nextMonthFirthDay);
  var oneDay = 1000 * 60 * 60 * 24;// 
  var endDay = new Date(nextMonthFirthDay - oneDay);
  var day = endDay.getDate(); // 
  console.log(day);
  day = day < 10 ? '0' + day : day; //  0

  // 
  return year + '-' + month + '-' + day;
};
